<template>
  <div class="back">
    <v-breadcrumbs :items="breadList" class="ml-12">
      <template v-slot:divider>
        <v-icon>mdi-chevron-right</v-icon>
      </template>
    </v-breadcrumbs>
    <v-card class="mx-auto mb-12" max-width="1000" min-height="1100">
      <v-form class="pl-8" ref="form">
        <div class="pt-8"><span class="red--text">*</span>第一步：项目名称</div>
        <v-col cols="12" sm="6">
          <v-text-field
            required
            placeholder="请输入项目名称，1-20个字以内"
            :rules="[v => !!v || '项目名称为必填项']"
            v-model="form.enterpriseneeds.title"
          ></v-text-field>
        </v-col>
        <p><span class="red--text">*</span>第二步：我的项目类型（可多选）</p>
        <v-row>
          <v-checkbox
            v-model="form.needs"
            class="ml-4 mx-2"
            label="船舶产业"
            value="船舶产业"
          ></v-checkbox>
          <v-checkbox
            v-model="form.needs"
            class="mx-2"
            label="金属材料"
            value="金属材料"
          ></v-checkbox>
          <v-checkbox
            v-model="form.needs"
            class="mx-2"
            label="能源石化"
            value="能源石化"
          ></v-checkbox>
          <v-checkbox
            v-model="form.needs"
            class="mx-2"
            label="汽车配件"
            value="汽车配件"
          ></v-checkbox>
          <v-checkbox
            v-model="form.needs"
            class="mx-2"
            label="电机电器"
            value="电机电器"
          ></v-checkbox>
          <v-checkbox
            v-model="form.needs"
            class="mx-2"
            label="通（专）用设备"
            value="通（专）用设备"
          ></v-checkbox>
          <v-checkbox
            v-model="form.needs"
            class="mx-2"
            label="工程机械"
            value="工程机械"
          ></v-checkbox>
          <v-checkbox
            v-model="form.needs"
            class="mx-2"
            label="高端装备"
          ></v-checkbox>
          <v-checkbox
            v-model="form.needs"
            class="mx-2"
            label="新材料"
            value="新材料"
          ></v-checkbox>
          <v-checkbox
            v-model="form.needs"
            class="mx-2"
            label="生物医药"
            value="生物医药"
          ></v-checkbox>
          <v-checkbox
            v-model="form.needs"
            class="mx-2"
            label="电子信息"
            value="电子信息"
          ></v-checkbox
          ><v-checkbox
            v-model="form.needs"
            class="mx-2"
            label="节能环保"
            value="节能环保"
          ></v-checkbox>
          <v-checkbox
            v-model="form.needs"
            class="mx-2"
            label="其他"
            value="其他"
          ></v-checkbox>
        </v-row>
        <div class="mt-5">
          <span class="red--text pt-5">*</span>第三步：我的需求简介
        </div>
        <v-col cols="12" sm="8">
          <v-textarea
            filled
            v-model="form.enterpriseneeds.intro"
            placeholder="请输入项目需求简介，200字以内"
            :rules="[v => !!v || '项目需求简介为必填项']"
          ></v-textarea>
        </v-col>
        <div class="mt-5"><span class="red--text">*</span>第四步：项目预算</div>
        <v-col cols="12" sm="6">
          <v-text-field
            required
            placeholder="请输入您的资金预算（元）"
            v-model="form.enterpriseneeds.budget"
            :rules="[v => !!v || '资金预算为必填项']"
          ></v-text-field>
        </v-col>
        <div class="mt-5"><span class="red--text">*</span>第五步：联系方式</div>
        <v-col cols="12" sm="6">
          <v-text-field
            required
            filled
            label="联系人姓名"
            placeholder="请输入联系人姓名"
            v-model="form.enterpriseneeds.contacts"
            :rules="[v => !!v || '联系人姓名为必填项']"
          ></v-text-field>
          <v-text-field
            required
            filled
            label="联系人电话"
            placeholder="请输入联系人电话"
            v-model="form.enterpriseneeds.tel"
            :rules="[v => !!v || '联系人电话为必填项']"
          ></v-text-field>
          <v-text-field
            required
            filled
            label="联系邮箱"
            placeholder="请输入联系邮箱"
            v-model="form.enterpriseneeds.mail"
            :rules="[v => !!v || '联系人邮箱为必填项']"
          ></v-text-field>
        </v-col>
        <div class="mt-5">第六步：需求文档</div>
        <v-file-input
          v-model="file"
          accept="application/pdf, application/vnd.openxmlformats-officedocument.wordprocessingml.document, application/msword,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel"
          placeholder="只能上传pdf/docx/excel文件"
          label="科研论文上传"
          prepend-icon="mdi-paperclip"
        >
        </v-file-input>
        <v-row class="mt-8 mb-12 ml-2">
          <div class="mr-12 ml-3">
            <v-btn
              width="120px"
              depressed
              large
              color="info"
              :loading="loading"
              :disabled="loading"
              @click="release()"
              >发布需求</v-btn
            >
          </div>
          <div class="mr-12">
            <v-btn
              width="120px"
              class="white--text"
              depressed
              large
              color="#AAAAAA"
              @click="cancel()"
              >取消</v-btn
            >
          </div>
        </v-row>
      </v-form>
    </v-card>
  </div>
</template>

<script>
export default {
  name: 'ReleaseScience',
  computed: {},
  data() {
    return {
      form: {
        enterpriseneeds: {
          title: '',
          intro: '',
          budget: '',
          contacts: '',
          mail: '',
          tel: ''
        },
        fileurl: null,
        needs: []
      },
      file: null,
      loading: false,
      breadList: [
        {
          text: '您的位置：首页',
          disabled: false,
          href: '/company'
        },
        {
          text: '发布企业需求',
          disabled: true
        }
      ]
    }
  },
  methods: {
    clearInput() {
      this.form = {}
    },
    async release() {
      let valid = this.$refs.form.validate()
      if (!valid) return
      this.loading = true
      if (this.file) {
        let formData = new FormData()
        formData.append('File', this.file)
        let resFile = await this.$http.post('/api/business/upload', formData, {
          headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
        })
        if (resFile.data.code === 200) {
          this.form.fileurl = resFile.data.data
        }
      }
      let res = await this.$http.post('/api/business/add', this.form, {
        headers: { 'Content-Type': 'application/json' }
      })
      if (res.data.code === 200) {
        this.$message.success('发布企业需求成功！')
        this.clearInput()
        this.$router.push('/company')
      } else {
        this.$message.error('发布企业需求失败！')
      }
    },
    cancel() {
      this.clearInput()
      this.$router.push('/company')
    }
  }
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
</style>
